import React, {useState, useEffect} from 'react';
import ArticleList from '../components/ArticleList';
import CommentList from '../components/CommentList';
import UpvoteSection from '../components/UpvoteSection';
import AddCommentForm from '../components/AddCommentForm';

import articleContent from '../data/article-content';

const ArticlePage = ({match}) => {
  const nameQueryParam = match.params.name;
  const currentArticle = articleContent.find(article => article.name === nameQueryParam);

  const [articleInfo, setArticleInfo] = useState({upvotes: 0, comments: []});

  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${nameQueryParam}`);
      const body = await result.json();
      setArticleInfo(body);
    };

    fetchData();
  }, [nameQueryParam]);

  return (
    <>
      {currentArticle && currentArticle.name ? (
        <>
          <h1>This is the {currentArticle.title} article</h1>
          <UpvoteSection upvotes={articleInfo.upvotes} articleName={nameQueryParam} setArticleInfo={setArticleInfo} />
          {currentArticle.content.map((paragraph, index) => (
            <p key={index}>{paragraph}</p>
          ))}
          <hr />
          <br />
          <CommentList comments={articleInfo.comments} />
          <AddCommentForm articleName={nameQueryParam} setArticleInfo={setArticleInfo} />
          <h3>We also recommend the following articles:</h3>
          <ArticleList articleList={articleContent.filter(otherArticle => otherArticle.name !== currentArticle.name)} />
        </>
      ) : (
        <h1>
          Article with name <span style={{color: 'cornflowerblue'}}>{nameQueryParam}</span> does not exist{' '}
        </h1>
      )}
    </>
  );
};

export default ArticlePage;
